export default {
	data() {
		return {
			memberNickname: '',
			code: -1, //0成功150重名，其他失败
			memberId: '',
		};
	},
	onShow(){
		this.memberId = uni.getStorageSync('userInfo') ? JSON.parse(uni.getStorageSync('userInfo')).id : ''
	},
	methods:{
		checkName(){
			// 验证用户新昵称
			if(this.memberNickname){
				this.$Request.get(`/appMemberController.do?checkNickname&memberId=${this.memberId}&memberNickname=${this.memberNickname}`)
				.then(res => {
					this.code = res.code
					if(res.code == 0 || res.code == 150){
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showToast({
					title: '请输入昵称！',
					icon: 'none'
				})
			}
		},
		clickChange(){
			if(this.memberNickname){
				this.$Request.get(`/appMemberController.do?updateNickname&memberId=${this.memberId}&memberNickname=${this.memberNickname}`)
				.then(res => {
					if(res.code == 0){
						uni.showToast({
							title: '昵称修改成功！',
							icon: 'none'
						})
						setTimeout(()=>{
							uni.navigateTo({
								url: '/pages/mine/editUserInfo'
							})
						},1000)
					}else if(res.code == 100){
						uni.showModal({
						    title: '提示',
						    content: '您尚未登录，是否去登录？',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'/pages/loginAll/login'
						            })
						        } else if (res.cancel) {
						            console.log('用户点击取消');
						        }
						    }
						});
					}else if(res.code == 150){
						this.code = 150
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showToast({
					title: '请输入昵称！',
					icon: 'none'
				})
			}
		}
	}
}
